import CondicionAccesorio from "../models/CondicionAccesorio";
import Anio from "../models/Anio";
import Condicion from "../models/Condicion";

const fileController = {};

fileController.getAll = async (req, res) => {
    try {
        const query = await Sale.find()
            .sort({
                fecha_cancelacion: -1,
            })
            .populate({
                path: "vendedor",
                select: "name sucursal",
            })
            .populate({
                path: "auto",
                select: "model version cod_tdp",
                populate: {
                    path: "model",
                    select: "marca name avatar",
                    populate: {
                        path: "marca",
                        select: "name avatar",
                    },
                },
            })
            .populate({
                path: "sucursalE",
                select: "name",
            })
            .populate({
                path: "colorE",
                select: "name",
            })
            .populate({
                path: "anioFabricacionE",
                select: "name",
            })
            .populate({
                path: "anioModeloE",
                select: "name",
            })
            .populate({
                path: "ubicacionVehiculoE",
                select: "name",
            })
            .populate({
                path: "estadoVehiculoE",
                select: "name",
            })
            .populate({
                path: "financiamientoE",
                select: "name",
            })
            .populate({
                path: "bancoE",
                select: "name",
            })
            .populate({
                path: "cliente",
                select: "name document",
            })
            .populate({
                path: "tipoOperacionE",
                select: "name document",
            })
            .populate({
                path: "tipoComprobanteE",
                select: "name document",
            })
            .populate({
                path: "estadoVentaE",
                select: "name document",
            })
            .populate({
                path: "estadoFacturacionE",
                select: "name document",
            })
            .populate("campanias")
            .populate("adicional")
            .populate("accesorios")
            .populate({
                path: "empleado",
                select: "name username",
            })
            .populate({
                path: "createdBy",
                select: "name username",
                strictPopulate: true,
            });

        if (query.length > 0) {
            res.json({ total: query, all: query });
        } else {
            return res.status(404).json({ message: `No existen expedientes` });
        }
    } catch (err) {
        console.log(err);
        return res.status(503).json({ message: err.message });
    }
};

